<template>
  <div class="pharmacyInfo">
    <van-nav-bar
      title="药店信息"
      fixed
      @click-left="$toPage(-1)"
      @click-right="onClickModify"
      >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
      <span slot="right" style="font-size: 15px">{{ rightClick }}</span>
    </van-nav-bar>
    <van-cell-group>
      <van-field
        v-model="userInfo.storeName"
        label="药店名称"
        placeholder="请输入药店名称"
        size="large"
        :border="false"
        required
        :disabled="disabled"
      />
      <van-field
        v-model="city"
        label="城市"
        placeholder="选择药店所处城市"
        is-link
        size="large"
        readonly
        required
        :disabled="disabled"
        @click="reg_select_city"
      />
      <van-field
        v-model="userInfo.address"
        label="详细地址"
        placeholder="请输入药店注册地址"
        size="large"
        :border="false"
        required
        :disabled="disabled"
      />
    </van-cell-group>
    <p class="tips_0">资质信息</p>
    <p class="tips_1">证件越齐全，越快通过审核~</p>
    <van-tabs v-model="type" type="card" animated @click="tabClick">
      <van-tab title="医疗单位" name="0">
        <ul class="reg_license">
          <li v-for="(reg,index) in reg_info" :key="index" v-if="reg.isShow">
            <div v-show="disabled" class="reg_src showDiv" @click="viewImg(index)"></div>
            <div v-show="!reg.url" class="reg_src" @click="reg_img_select(index)" />
            <img v-show="reg.url" :src="reg.url" @click="imgChange(index)">
            {{ reg.name }}
          </li>
        </ul>
      </van-tab>
      <van-tab title="药品经营企业" name="1">
        <ul class="reg_license">
          <li v-for="(reg,index) in reg_info" :key="index" v-if="reg.isShow">
            <div v-show="disabled" class="reg_src showDiv" @click="viewImg(index)"></div>
            <div v-show="!reg.url" class="reg_src" @click="reg_img_select(index)" />
            <img v-show="reg.url" :src="reg.url" @click.stop.prevent="imgChange(index)">
            {{ reg.name }}
          </li>
        </ul>
      </van-tab>
    </van-tabs>
    <van-popup v-model="show">
      <p @click="viewImg(uploadIndex)">
        {{ $t("messages.regist.lookImg") }}
      </p>
      <p @click="reg_img_select(uploadIndex)">
        {{ $t("messages.regist.changeImg") }}
      </p>
    </van-popup>
    <van-image-preview
      v-model="mask"
      lazy-load
      :images="showImg"
    />
    <van-button
      type="danger"
      size="large"
      block
      :disabled="disabled"
      @click="btnMyInvoice"
    >
      提交
    </van-button>
    <div
      v-show="loading"
      class="loading"
    >
      <div>
        <van-loading
          size="36px"
          vertical
          type="spinner"
          color="#1989fa"
        />
        <p>信息上传中...</p>
      </div>
    </div>
  </div>
</template>
<script>
import { Dialog } from 'vant';
export default {
  data() {
    return {
      reg_info: [
        {
          id: 10,
          isShow: true,
          fileList: [],
          url: "",
          name: "营业执照",
          message: "请上传营业执照"
        },
        {
          id: 11,
          isShow: false,
          fileList: [],
          url: "",
          name: "药店经营许可证",
          message: "请上传药店经营许可证"
        },
        {
          id: 12,
          isShow: false,
          fileList: [],
          url: "",
          name: "GSP认证",
          message: "请上传GSP认证"
        },
        {
          id: 15,
          isShow: true,
          fileList: [],
          url: "",
          name: "医疗机构执业许可证",
          message: "请上传医疗机构执业许可证"
        },
        {
          id: 13,
          isShow: true,
          fileList: [],
          url: "",
          name: "采购提货人委托书",
          message: "请上传采购提货人委托书"
        },
        {
          id: 14,
          isShow: true,
          fileList: [],
          url: "",
          name: "采购提货人身份证",
          message: "请上传采购提货人身份证"
        },
        {
          id: 16,
          isShow: true,
          fileList: [],
          url: "",
          name: "诊疗科目副本",
          message: "请上传诊疗科目副本"
        }
      ],
      show: false,
      uploadIndex: 0,
      mask: false,
      showImg: [],
      loading: false,
      userInfo: {},
      city: '',
      disabled: true,
      rightClick: '修改',
      type: 0
    }
  },
  mounted() {
    this.getInfo()
  },
  methods: {
    //  修改信息
    onClickModify() {
      if (!this.disabled) {
        this.rightClick = '修改'
        this.disabled = true
      }else {
        Dialog.confirm({
          title: '温馨提示',
          message: '修改药店信息后需要重新审核~'
        }).then(() => {
          this.rightClick = '取消'
          this.disabled = false
        })
      }
    },
    //  选择城市
    reg_select_city() {
      JNB._requestNative('areaSelect', (data) => {
        const dataObj = JSON.parse(data)
        this.city = dataObj.fullName
        this.userInfo.addressCode = dataObj.code
      })
    },
    //  城市码转城市名称
    reg_select_cityCode() {
        JNB._requestNative('convertAreaCodeToFullName', this.userInfo.addressCode, (fullName) => {
        this.city = fullName
      })
    },
    //  选择资质图片
    reg_img_select(index) {
      this.show = false
      JNB._requestNative(
        'openImageSelect',
        (url) => {
          if (url) {
            this.reg_info[index].url = url
          }
        })
    },
    //  更换图片
    imgChange(index) {
      // 选择图片
      this.show = true
      this.uploadIndex = index
    },
    //  展示大图
    viewImg(uploadIndex) {
      this.show = false
      if(this.reg_info[uploadIndex].url) {
        this.showImg[0] = this.reg_info[uploadIndex].url
        this.mask = true
      }
    },
    //  提交
    btnMyInvoice() {
      this.loading = true
      this.disabled = true
      this.resetInfo()
    },
    //  修改图片
    resetImg() {
      JNB._requestNative(
        JNB._transfer('globalServer', 'FileInfoModule', 'fileServerInfo'), JSON.stringify({ compid: this.userInfo.storeId }), (data) => {
          const list = []
          const o = JSON.parse(data)
          this.reg_info.forEach(element => {
            if (element.url.slice(0, 5) === 'image') {
              list.push({ remotePath: o.data.companyFilePath,
               fileName: element.id + '.jpg', uri: element.url })
            }
          })
          if (list.length > 0) {
            this.loading = true
            JNB._requestNative(
              'fileUpload',
              JSON.stringify({ url: o.data.upUrl, files: list }), (json) => {
                const data = JSON.parse(json)
                if (data.code === 200) {
                  this.loading = false
                  this.$toast('修改成功！')
                }else {
                  this.$toast.fail('资质修改失败！')
                  this.loading = false
                }
              })
          }else {
            this.loading = false
            this.$toast('修改成功！')
          }
        })
    },
    //  修改药店信息
    resetInfo() {
      JNB.query({
        serverName: 'userServer',
        cls: 'StoreManageModule',
        method: 'appUpdateStoreInfo',
        params: {
          storeName: this.userInfo.storeName,
          address: this.userInfo.address,
          addressCode: this.userInfo.addressCode,
          storetype: this.type
        },
        callback: (data) => {
          const result = JSON.parse(data)
          if (result.code === 200) {
            this.resetImg()
          }else {
            this.$toast.fail('信息修改失败！')
            this.loading = false
          }
        }
      })
    },
    //  获取用户信息
    getInfo() {
       let _this = this
       JNB.query({
          serverName:"userServer",
          cls:"LoginRegistrationModule",
          method:"getStoreSession",
          callback:(data)=>{
            const dataObj = JSON.parse(data)

            if (dataObj.code === 200) {
              _this.userInfo = dataObj.data.comp
              this.tabClick(dataObj.data.comp.storetype)
              //  调用城市码转城市名称
              _this.reg_select_cityCode()
              //  获取图片
              _this.getImg()
            }
          }
        })
    },
    //  获取图片信息
    getImg() {
      JNB.query({
        serverName: 'globalServer',
        cls: 'FileInfoModule',
        method: 'fileServerInfo',
        params: {
          compid: this.userInfo.storeId
        },
        callback: (data) => {
          const result = JSON.parse(data)
          if (result.code === 200) {
          // 更新用户登录状态
          // const imgSrc = result.data.downPrev + result.data.companyFilePath
          const serverPath = result.data.ergodicUrl//当前文件图片所在服务器地址
          const imgSrc = result.data.companyFilePath //当前企业图片所在路径地址

          var xhr = new XMLHttpRequest()
          xhr.onreadystatechange = () => {
            if (xhr.readyState === 4 && xhr.status === 200) {
              var obj = xhr.responseText
              data = JSON.parse(obj).data.sort()
              for (var i in data) {
                if (data[i].indexOf("jpg") > 0) {
                  this.reg_info.forEach(arr => {
                    if(arr.id == data[i].slice(1,3)){
                      arr.url = result.data.downPrev + imgSrc + data[i] + '?' + new Date().getTime()
                    }
                  })

                }
              }
            }
          }
          xhr.open("POST", serverPath, true);
          xhr.setRequestHeader("specify-path", imgSrc);
          xhr.setRequestHeader("ergodic-sub", "false");
          xhr.send(null);
        }else {
          this.$toast.fail(result.message)
        }
        }
      })
    },
    //  切换tab
    tabClick(index) {
      if(index === 1){
        this.reg_info[1].isShow = true
        this.reg_info[2].isShow = true
        this.reg_info[3].isShow = false
        this.reg_info[6].isShow = false
        this.type = 1
      }else{
        this.reg_info[1].isShow = false
        this.reg_info[2].isShow = false
        this.reg_info[3].isShow = true
        this.reg_info[6].isShow = true
        this.type = 0
      }
    }
  }
}
</script>
<style scoped>
.pharmacyInfo{
  overflow: hidden;
  padding: 92px 0 0;
  box-sizing: border-box;
}
.pharmacyInfo .van-tabs{
  margin-top: 30px;
  z-index: 0;
}
.pharmacyInfo .van-tabs ul {
  margin-top: 20px;
}
.pharmacyInfo .reg_license li{
  width:344px;
  height:343px;
  border:1px solid rgba(230,230,230,1);
  line-height: 343px;
  text-align: center;
  font-size: 16px;
  display: inline-block;
  margin: 18px 0 0 18px;
  position: relative;
  background: url('..\..\assets\reg_icon\add.png') no-repeat;
  background-size: 70%;
  background-position: center center;
}
.pharmacyInfo .reg_license li img,.pharmacyInfo .reg_license li .reg_src{
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.pharmacyInfo .reg_license li .showDiv{
  z-index: 99;
}
.pharmacyInfo .van-popup{
  width: 300px;
  padding: 0 50px;
}
.pharmacyInfo .van-popup p{
  padding: 10px;
  user-select: none;
}
.pharmacyInfo .tips_0{
  padding: 0 30px;
}
.pharmacyInfo .tips_1{
  padding: 0 50px 0;
  font-size: 14px;
  color: #999;
}
.pharmacyInfo .van-button{
  width: 710px;
  border-radius: 15px;
  margin: 30px auto 20px;
}
.pharmacyInfo .loading{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: rgba(0,0,0,0.3);
  z-index: 99;
}
.pharmacyInfo .loading div{
  margin: auto;
  font-size: 36px;
}
</style>

